@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Work Sans', sans-serif;
    font-weight: 700;
    background: #1c92d2;
    color:black;

}
.coin-app{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: black;
}
.coin-search{
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.coin-text{
    margin-bottom: 32px;
    text-align: center;
}
.coin-input{
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background: #654ea3;  
    background: linear-gradient(to right, #eaafc8, #654ea3); 
    color: black;

}
.coin-input:-ms-input-placeholder{
    color: white;
}
.coin-input::placeholder{
    color: white;
}

.coin-container{
    display: flex;
    justify-content: center;
}
.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid white;
    width: 900px;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}
.coin-price{
    width: 110px;
}
.coin-volume{
    width: 230px;
}
.coin-marketcap{
    width: 155px;
}
.coin-percent{
    width: 80px;
}
.red{
    color: red
}
.green{
    color: green;
}
